import BaseAbovePageHeader from '@salesforce/retail-react-app/app/pages/product-list/partials/above-page-header'
import React from 'react'
import PropTypes from 'prop-types'
import {useParams} from 'react-router-dom'
import {getConfig} from '@salesforce/pwa-kit-runtime/utils/ssr-config'
import useMultiSite from '@salesforce/retail-react-app/app/hooks/use-multi-site'
import {useAccessToken} from '@salesforce/commerce-sdk-react'
import {useQuery} from '@tanstack/react-query'
import {jsxToReact} from '@salesforce/retail-react-app/app/components/page-designer/headless/helpers'
import {getAppOrigin} from '@salesforce/pwa-kit-react-sdk/utils/url'

/**
 * Get a category slot as JSON
 */
function useCategorySlot(categoryId, slotId) {
    const {
        app: {commerceAPI}
    } = getConfig()
    const {
        parameters: {organizationId}
    } = commerceAPI
    const appOrigin = getAppOrigin()

    const {site} = useMultiSite()
    const {getTokenWhenReady} = useAccessToken()

    return useQuery({
        queryKey: ['custom', 'content-slots', 'category', categoryId, slotId],
        queryFn: async () => {
            const urlParams = new URLSearchParams()
            urlParams.append('siteId', site.id)
            const token = await getTokenWhenReady()
            const response = await fetch(
                `${appOrigin}/${commerceAPI.proxyPath.replace(
                    /^\/|\/$/g,
                    ''
                )}/custom/content-slots/v1/organizations/${organizationId}/category/${categoryId}/${slotId}?${urlParams.toString()}`,
                {
                    headers: {
                        Authorization: `Bearer ${token}`
                    }
                }
            )
            if (!response.ok) {
                //throw new Error(`Could not load slot: ${response.status}`)
                // be safe
                return []
            }
            return await response.json()
        },
        enabled: !!(categoryId && slotId)
    })
}

/**
 * Render the category header slot if it contains JSX content
 *
 * Uses the jsxToReact helper from Page Designer impl to render the JSX content
 */
function CategoryHeaderSlot({categoryId}) {
    const {data: slotData, isSuccess} = useCategorySlot(categoryId, 'pwa-category-header')

    return (
        <>
            {isSuccess ? (
                <>
                    {slotData?.slot?.filter(Boolean).map((slot, index) => {
                        return (
                            <div key={index}>
                                {slot.jsx ? <div>{jsxToReact(slot.jsx, {})}</div> : null}
                            </div>
                        )
                    })}
                </>
            ) : null}
        </>
    )
}

CategoryHeaderSlot.propTypes = {
    categoryId: PropTypes.string.isRequired
}

const AbovePageHeader = () => {
    const params = useParams()
    const categoryId = params.categoryId
    return (
        <>
            <CategoryHeaderSlot categoryId={categoryId} />
            <BaseAbovePageHeader />
        </>
    )
}

export default AbovePageHeader
